.university {
  display: flex;
  gap: 16px;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-height: 228px;
}

.info > * {
  padding: 32px;
}

.info p {
  font-family: Montserrat;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  text-align: center;
}

.title {
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 1.25px;
  text-align: center;
}

.controls {
  gap: 16px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.description {
  background: #fff;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.5px;
  text-align: left;
  padding: 32px;
}

.description > * {
  padding: 32px;
}

.universityIcon {
  border-radius: 100%;
}
